___CSS_LOADER_EXPORT___.push([module.id, "\n.explorerContainer[data-v-578512f6] {\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/sidebars/explorer.vue"],"names":[],"mappings":";AAyCA;AACA","sourcesContent":["<script setup lang=\"ts\">\nimport { onMounted, ref, watch } from 'vue';\nimport { container } from '../../../../domain/core/diContainer';\nimport { IFileExplorer } from '../../../../domain/useCase/IFileExplorer';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { IViewModel } from '../../../../domain/viewModel/IViewModel';\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst rootDiv = ref<HTMLDivElement>();\n\nconst openApp = async () => {\n    executor.execute(async () => {\n        while (rootDiv.value!.firstElementChild) { rootDiv.value!.removeChild(rootDiv.value!.firstElementChild); }\n        const fileExplorer = await executor.resolveUseCase<IFileExplorer>(IFileExplorer);\n        await fileExplorer.loadFileExplorer(rootDiv.value!);\n    }, { loading: true });\n};\n\nlet curAppId = viewModel.studio.appId!;\nwatch(viewModel, (val, oldVal) => {\n    if (!val.studio.appId) { return; }\n    if (val.studio.appId == curAppId) { return; }\n\n    curAppId = val.studio.appId;\n    openApp();\n});\n\n/* IMPORTANT: This \"watch\" code below will always run after \"UI Settings\" implementation,\n    because it always manipulates \"viewmodel studio items\" to set the initial(default) models(modelbody) to UI settings models. \n    Therefore, if we open this onMounted code, function \"openApp\" runs double.\n*/\n// onMounted(async () => {\n//     openApp();\n// });\n\n</script>\n<template>\n    <div class=\"explorerContainer\" ref=\"rootDiv\"></div>\n</template>\n<style scoped>\n.explorerContainer {\n}\n</style>"],"sourceRoot":""}]);
