ding-box;\n    opacity: 1;\n    cursor: pointer;\n    line-height: 30px;\n    color: #151233;\n}\n.left-opacity-gradient[data-v-7835a17c] {\n    z-index: 1;\n    left: 0px;\n    position: absolute;\n    width: 77px;\n    height: 30px;\n    background-image: linear-gradient(to right, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));\n    pointer-events: none;\n}\n.right-opacity-gradient[data-v-7835a17c] {\n    position: absolute;\n    right: 0px;\n    z-index: 1;\n    width: 77px;\n    height: 30px;\n    background-image: linear-gradient(to left, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));\n    pointer-events: none;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/ui/tabs.vue"],"names":[],"mappings":";AA2MA;IACI,YAAY;AAChB;AAEA;IACI,aAAa;IACb,yBAAyB;IACzB,kBAAkB;IAClB,kBAAkB;IAClB,iBAAiB;AACrB;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,eAAe;IACf,gBAAgB;IAChB,aAAa;IACb,oBAAoB;AACxB;AAEA;IACI,8BAA8B;IAC9B,YAAY;IACZ,YAAY;AAChB;AAEA;IACI,aAAa;IACb,iBAAiB;AACrB;AAEA;IACI,eAAe;IACf,wBAAwB;AAC5B;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,gBAAgB;IAChB,mBAAmB;AACvB;AAEA;IACI,cAAc;IACd,iBAAiB;AACrB;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,iBAAiB;IACjB,cAAc;AAClB;;AAEA;;GAEG;AAEH;IACI,WAAW;IACX,mBAAmB;AACvB;AACA;IACI,YAAY;IACZ,mBAAmB;AACvB;AAEA;IACI,UAAU;AACd;AAEA;IACI,wBAAwB;AAC5B;AAEA;IACI,cAAc;AAClB;AACA;IACI,cAAc;AAClB;AACA;IACI,cAAc;AAClB;AAEA;IACI,uBAAuB;IACvB,8BAA8B;IAC9B,mBAAmB;IACnB,kCAAkC;IAClC,uBAAuB;IACvB,wCAAwC;AAC5C;AAEA;IACI,UAAU;IACV,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,4DAA4D;IAC5D,UAAU;IACV,eAAe;IACf,iBAAiB;IACjB,cAAc;AAClB;AAEA;IACI,UAAU;IACV,SAAS;IACT,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,2FAA2F;IAC3F,oBAAoB;AACxB;AACA;IACI,kBAAkB;IAClB,UAAU;IACV,UAAU;IACV,WAAW;IACX,YAAY;IACZ,0FAA0F;IAC1F,oBAAoB;AACxB","sourcesContent":["<script setup lang=\"ts\">\nimport { onBeforeUnmount, onMounted, onUpdated, ref } from \"vue\";\nimport { ITab, ITabs } from \"./ITabs\";\nimport { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\nimport { container } from \"../../../../domain/core/diContainer\";\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst currentItemID = ref<string>();\ninterface ITabInternal extends ITab {\n    mouseOver?: boolean;\n    mouseOverActions?: boolean;\n    closeVisible?: boolean;\n}\ninterface ITabsPropsInternal extends ITabsProps {\n    tabs: Array<ITabInternal>;\n}\ninterface ITabsProps {\n    tabs: Array<ITab>;\n    height?: string;\n}\n\nconst props: Readonly<ITabsPropsInternal> = defineProps<ITabsProps>();\nconst tabContainer = ref<HTMLDivElement>(null as any);\nconst showLeftChevron = ref(false);\nconst showRightChevron = ref(false);\n\nconst wheelHandler = (event: WheelEvent) => {\n    event.preventDefault();\n    tabContainer.value.scrollLeft += event.deltaY / 2;\n};\nonMounted(() => {\n    tabContainer.value.addEventListener(\"wheel\", wheelHandler);\n});\n\nonBeforeUnmount(() => {\n    tabContainer.value.removeEventListener(\"wheel\", wheelHandler);\n});\n\nfunction mouseOver(tab: ITabInternal, over: boolean) {\n    tab.mouseOver = over;\n    tab.closeVisible = showClose(tab);\n}\nfunction mouseOverActions(tab: ITabInternal, over: boolean) {\n    tab.mouseOverActions = over;\n    tab.closeVisible = showClose(tab);\n}\n\nfunction showClose(tab: ITabInternal) {\n    if (tab.mouseOverActions) {\n        return true;\n    }\n    if (tab.modified) {\n        return false;\n    }\n    if (tab.mouseOver) {\n        return true;\n    }\n    return false;\n}\nfunction closeTab(tab: ITabInternal, index: number) {\n    if (!tab.synced) {\n        return;\n    }\n    tab.cb?.close?.();\n}\nfunction selectTab(tab: ITabInternal) {\n    if (!tab.synced) {\n        return;\n    }\n    if (tab.selected) {\n        return;\n    }\n    tab.cb?.select?.();\n    props.tabs.forEach((eachTab) => (eachTab.selected = false));\n    tab.selected = true;\n}\n\nfunction getTabIcon(tabType: string) {\n    if (tabType == \"qjson\") {\n        return \"mdi mdi mdi-palette qjsonType\";\n    }\n    if (tabType == \"process\") {\n        return \"mdi mdi mdi-auto-fix processType\";\n    }\n    if (tabType == \"entityDesigner\") {\n        return \"mdi mdi-database entityType\";\n    }\n    if (tabType == \"bpmn\") {\n        return \"mdi mdi-vector-polyline bpmnType\";\n    }\n}\ndefineExpose({ selectTab } as ITabs);\n\nconst isOverFlow = ref(tabContainer.value?.scrollWidth > tabContainer.value?.clientWidth);\n\nonUpdated(() => {\n    isOverFlow.value = tabContainer.value?.scrollWidth > tabContainer.value?.clientWidth;\n    const vmCurrentItemID = viewModel.studio.currentItem?.ID;\n\n    if (vmCurrentItemID) {\n        if (isOverFlow.value && vmCurrentItemID != currentItemID.value) {\n            const firstTab = props.tabs[0];\n            const lastTab = props.tabs[props.tabs.length - 1];\n            const currentItemPosition = document.getElementById(vmCurrentItemID)!.getBoundingClientRect();\n            const tabContainerPosition = tabContainer.value.getBoundingClientRect();\n            if (lastTab && lastTab.id == vmCurrentItemID) {\n                tabContainer.value.scroll(Number.MAX_SAFE_INTEGER, 0);\n                showRightChevron.value = false;\n                showLeftChevron.value = true;\n            } else {\n                if (firstTab && firstTab.id == vmCurrentItemID) {\n                    tabContainer.value.scroll(0, 0);\n                    showRightChevron.value = true;\n                    showLeftChevron.value = false;\n                } else if (currentItemPosition.left - tabContainerPosition.left < 0) {\n                    tabContainer.value.scroll(\n                        Math.abs(document.getElementById(firstTab.id)?.getBoundingClientRect().left!) + currentItemPosition.left,\n                        0\n                    );\n                    if (tabContainerPosition.right - document.getElementById(lastTab.id)?.getBoundingClientRect().right! < 0) {\n                        showRightChevron.value = true;\n                    }\n                } else if (tabContainerPosition.right - currentItemPosition.right < 0) {\n                    tabContainer.value.scroll(tabContainer.value.scrollLeft + currentItemPosition.right - tabContainerPosition.right, 0);\n                    if (document.getElementById(firstTab.id)?.getBoundingClientRect().left! - tabContainerPosition.left < 0) {\n                        showLeftChevron.value = true;\n                    }\n                }\n            }\n            currentItemID.value = vmCurrentItemID;\n        }\n    }\n});\n\nfunction scrollRight() {\n    tabContainer.value.scrollBy(300, 0);\n    checkChevrons();\n}\nfunction scrollLeft() {\n    tabContainer.value.scrollBy(-300, 0);\n    checkChevrons();\n}\n\nfunction checkChevrons($event?: any) {\n    let mouseWhellScroll = 0;\n    const tolerance = 5;\n    if ($event) {\n        mouseWhellScroll = $event.deltaY / 2;\n    }\n    if (\n        document.getElementById(props.tabs[0].id)!.getBoundingClientRect().left -\n            tabContainer.value.getBoundingClientRect().left -\n            mouseWhellScroll +\n            tolerance <\n        0\n    ) {\n        showLeftChevron.value = true;\n    } else {\n        showLeftChevron.value = false;\n    }\n    if (\n        tabContainer.value.getBoundingClientRect().right -\n            document.getElementById(props.tabs[props.tabs.length - 1].id)!.getBoundingClientRect().right +\n            mouseWhellScroll +\n            tolerance <\n        0\n    ) {\n        showRightChevron.value = true;\n    } else {\n        showRightChevron.value = false;\n    }\n}\n</script>\n<template>\n    <i class=\"mdi mdi-chevron-left scroll\" @click=\"scrollLeft\" v-if=\"isOverFlow && showLeftChevron\" />\n    <div class=\"tab-container\" ref=\"tabContainer\">\n        <div class=\"left-opacity-gradient\" v-if=\"isOverFlow && showLeftChevron\"></div>\n        <div class=\"right-opacity-gradient\" v-if=\"isOverFlow && showRightChevron\"></div>\n        <div\n            v-for=\"(tab, index) in props.tabs\"\n            :class=\"{ hayal: !tab.synced }\"\n            @mouseover=\"mouseOver(tab, true)\"\n            :id=\"tab.id\"\n            @mouseleave=\"mouseOver(tab, false)\"\n            :title=\"tab.name\"\n            @wheel=\"checkChevrons\"\n        >\n            <div class=\"tab-column\" :style=\"{ lineHeight: props.height }\" :class=\"{ 'tab-selected': tab.selected }\" @click=\"selectTab(tab)\">\n                <div class=\"tab-content\">\n                    <div style=\"padding-right: 5px\" :class=\"getTabIcon(tab.type)\"></div>\n                    <div v-if=\"tab.name.length < 21\" class=\"tab-text\">{{ tab.name }}</div>\n                    <div v-else class=\"tab-text\">{{ tab.name.substring(0, 20) + \"...\" }}</div>\n                </div>\n                <div class=\"tab-actions\" @mouseover=\"mouseOverActions(tab, true)\" @mouseleave=\"mouseOverActions(tab, false)\">\n                    <div class=\"tab-modified\" v-show=\"!tab.mouseOverActions && tab.modified\"></div>\n                    <div class=\"tab-close\" v-show=\"tab.closeVisible\" @click.stop=\"closeTab(tab, index)\">X</div>\n                </div>\n            </div>\n        </div>\n    </div>\n    <i class=\"mdi mdi-chevron-right scroll\" @click=\"scrollRight\" v-if=\"isOverFlow && showRightChevron\" />\n</template>\n<style scoped>\n.hayal {\n    opacity: 0.3;\n}\n\n.tab-container {\n    display: flex;\n    color: rgb(163, 163, 163);\n    overflow-y: hidden;\n    overflow-x: hidden;\n    user-select: none;\n}\n\n.tab-column {\n    position: relative;\n    width: 100%;\n    cursor: pointer;\n    padding: 0px 4px;\n    display: flex;\n    filter: grayscale(1);\n}\n\n.tab-selected {\n    border-bottom: 2px solid black;\n    color: white;\n    filter: none;\n}\n\n.tab-content {\n    display: flex;\n    /* flex:1 1 0%; */\n}\n\n.tab-icon {\n    min-width: 10px;\n    color: rgb(68, 157, 209);\n}\n\n.tab-actions {\n    min-width: 26px;\n    padding: 0px 7px;\n    margin-top: auto;\n    margin-bottom: auto;\n}\n\n.tab-modified {\n    font-size: 6px;\n    padding-left: 2px;\n}\n\n.tab-close {\n    font-size: 14px;\n    padding-left: 2px;\n    line-height: 18px;\n    color: #50505a;\n}\n\n/* .tab-close:hover {\n    background-color: #50505a;\n} */\n\n.tab-text {\n    color: grey;\n    white-space: nowrap;\n}\n.tab-selected .tab-text {\n    color: black;\n    white-space: nowrap;\n}\n\n.last-tab {\n    opacity: 1;\n}\n\n.qjsonType {\n    color: rgb(68, 157, 209);\n}\n\n.processType {\n    color: #007c77;\n}\n.bpmnType {\n    color: #007c77;\n}\n.entityType {\n    color: #e9724c;\n}\n\n.tab-container::-webkit-scrollbar-thumb:active {\n    background-color: green;\n    /* color of the scroll thumb */\n    border-radius: 20px;\n    /* roundness of the scroll thumb */\n    border: 2px solid green;\n    /* creates padding around scroll thumb */\n}\n\n.scroll {\n    z-index: 2;\n    width: 28px;\n    height: 29px;\n    /* UI Properties */\n    background: var(--grey-darken-4) 0% 0% no-repeat padding-box;\n    opacity: 1;\n    cursor: pointer;\n    line-height: 30px;\n    color: #151233;\n}\n\n.left-opacity-gradient {\n    z-index: 1;\n    left: 0px;\n    position: absolute;\n    width: 77px;\n    height: 30px;\n    background-image: linear-gradient(to right, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));\n    pointer-events: none;\n}\n.right-opacity-gradient {\n    position: absolute;\n    right: 0px;\n    z-index: 1;\n    width: 77px;\n    height: 30px;\n    background-image: linear-gradient(to left, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));\n    pointer-events: none;\n}\n</style>\n"],"sourceRoot":""}]);
