function filterTreeview() {
  if (itemsCache.value.length < viewModel.studio.items.length) {
    itemsCache.value = itemsCache.value.concat(viewModel.studio.items.filter((item) => itemsCache.value.findIndex((i) => i.ID == item.ID) == -1));
  } else if (itemsCache.value.filter.length > viewModel.studio.items.length) {
    itemsCache.value = itemsCache.value.filter(
      (item) => item.objectType != "module" || viewModel.studio.items.findIndex((i) => i.ID == item.ID) != -1
    );
  }
  treeviewFilterData.value = {};
  pagesFilterData.value = {};
  entitiesFilterData.value = {};
  processesFilterData.value = {};

  itemsCache.value.forEach((item) => {
    switch (item.objectType) {
      case "module":
        pagesFilterData.value[item.ID] = [];
        entitiesFilterData.value[item.ID] = [];
        processesFilterData.value[item.ID] = [];
        (item as IModule).children.forEach((children) => {
          if (children.objectType === "model") {
            if (children.modelType === "qjson" || (children as IModel).modelType == "js" || (children as IModel).modelType === "yaml") {
              pagesFilterData.value[item.ID].push(children);
            } else if (children.modelType === "entityDesigner") {
              entitiesFilterData.value[item.ID].push(children);
            } else if (children.modelType === "process" || children.modelType === "bpmn") {
              processesFilterData.value[item.ID].push(children);
            }
          }
        });
        break;
      case "model":
      case "folder": // TODO: This case have to be approved when folder system arrived.
        treeviewFilterData.value[item.ID] = [item];
        break;
      default:
        break;
    }
  });
}

function createPopupOpen() {
  if (createPopupVisible.value == false) {
    createPopupVisible.value = true;
  } else {
    createPopupVisible.value = false;
  }
}

function toggleFilter(type: "ui" | "entity" | "process") {
  switch (type) {
    case "ui":
      viewModel.studio.filters.ui = showPages.value = !showPages.value;
      break;
    case "entity":
      viewModel.studio.filters.entity = showEntities.value = !showEntities.value;
      break;
    case "process":
      viewModel.studio.filters.process = showProcesses.value = !showProcesses.value;
      break;
    default:
      break;
  }
  if (cacheItems) {
    itemsCache.value = viewModel.studio.items;
    cacheItems = false;
  }
  filterTreeview();
  showModels();
}

function showModels() {
  filteredModels.value = [];
  if (showPages.value) filteredModels.value.push("qjson");
  if (showEntities.value) filteredModels.value.push("entityDesigner");
  if (showProcesses.value) {
    filteredModels.value.push("process");
    filteredModels.value.push("bpmn");
  }

  sidebarManager.reset();
  viewModel.studio.items = itemsCache.value
    .map((item) => ({
      ...item,
      children: (showPages.value && item.objectType == "model" ? treeviewFilterData.value[item.ID] || [] : [])
        .concat(showPages.value ? pagesFilterData.value[item.ID] || [] : [])
        .concat(showEntities.value ? entitiesFilterData.value[item.ID] || [] : [])
        .concat(showProcesses.value ? processesFilterData.value[item.ID] || [] : []),
    }))
    .filter((item) => item.objectType == "module" || showPages.value);

  sidebarManager.show(item.sidebarComponent);
}

function searchItem() {
  if (cacheItems) filterTreeview();
  viewModel.studio.filters.text = searchInput.value;
  if (searchInput.value.length > 2) {
    resetSideBar.value = true;
    viewModel.studio.items = viewModel.studio.items
