udio } from \"../../../../domain/useCase/IStudio\";\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { IAsyncComponentCreator } from '../../../../domain/presentation/IAsyncComponentCreator';\nimport { IObjectUseCase } from '../../../../domain/useCase/IObjectUseCase';\nimport { defineAsyncComponent, onMounted, ref, watch } from 'vue';\nimport { TooltipImpl } from '../../impls/tooltipImpl';\nimport { computed } from '@vue/reactivity';\nimport { IHistoryUseCase } from '../../../../domain/useCase/IHistoryUseCase';\nimport { container } from '../../../../domain/core/diContainer';\nimport { IQJsonAdditionals } from '@stechquick/plateau-comm/dist/common/shrimp/interfaces/quickCloud/quickCloud';\nimport { ITopMenuButton } from '../../../../domain/model/ui/ITopMenuButton';\nconst studio = container.resolve<IStudio>(IStudio);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst compCreator = container.resolve<IAsyncComponentCreator>(IAsyncComponentCreator);\nconst objectUseCase = container.resolve<IObjectUseCase>(IObjectUseCase);\nconst historyUseCase = container.resolve<IHistoryUseCase>(IHistoryUseCase);\nconst saveDisabled = computed(() => !viewModel.studio.currentItem?.modified.model || !viewModel.studio.currentItem.checkedOut);\nconst bootstrap = require('bootstrap');\nconst tooltip = container.resolve<TooltipImpl>(TooltipImpl);\n\nfunction Save() {\n    if (saveDisabled.value) { return; }\n\n    executor.execute(async () => {\n        await studio.save(viewModel.studio.currentItem!)\n    }, { loading: true });\n\n}\n\nfunction History() {\n    executor.execute(async () => {\n        const item = viewModel.studio.currentItem!;\n        await historyUseCase.openHistoryDialog(item.ID, item.name);\n    }, { loading: true });\n}\n\nasync function Delete() {\n    await objectUseCase.deleteObject(viewModel.studio.currentItem?.ID!);\n}\n\nfunction Checkin() {\n    executor.execute(async () => {\n        dialog.showDialog(defineAsyncComponent(() => import(\"../../../vue3/components/dialogs/studio/savePlus.vue\")), { closable: true, title: viewModel.studio.currentItem?.name }, {item: viewModel.studio.currentItem});\n    }, { loading: true });\n\n}\nfunction Checkout() {\n    executor.execute(async () => {\n        await studio.checkout(viewModel.studio.currentItem!)\n    }, { loading: true });\n}\nfunction UndoCheckout() {\n    executor.execute(async () => {\n        await studio.release(viewModel.studio.currentItem!)\n    }, { loading: true });\n}\nfunction convertToCustomComp() {\n    executor.execute(async () => {\n        const modelAdditionals: IQJsonAdditionals = { qjsonType: 'quickComponent' }\n        const objectUseCase = container.resolve<IObjectUseCase>(IObjectUseCase);\n        await objectUseCase.updateObject(viewModel.studio.currentItem!.ID, { name: viewModel.studio.currentItem!.name, description: '', modelAdditionals });\n    }, { loading: true });\n}\nfunction topMenuButton(button: ITopMenuButton) {\n    if (button.disabled) { return; }\n    button.onSelect();\n}\n\nonMounted(() => {\n    tooltip.startTooltip()\n    watch(viewModel.studio.topMenu!, () => {tooltip.startTooltip();});\n});\n\n</script>\n<template>\n    <div class='topMenuDiv'>\n        <div class='col-3' style='display: flex;'>\n            <div @click=\"Save()\" data-toggle=\"tooltip\" title=\"Save\" placement='bottom'>\n\n                <i class='mdi mdi-content-save-outline topMenuIcons'\n                    :class=\"{ 'disabled-item': saveDisabled, disabled: saveDisabled }\"></i>\n            </div>\n            <div @click=\"History()\" data-toggle=\"tooltip\" title=\"History\" placement='bottom'>\n                <i class='mdi mdi-history topMenuIcons'></i>\n            </div>\n            <div v-show=\"viewModel.studio.currentItem?.checkedOut\" @click=\"Checkin()\" data-toggle=\"tooltip\"\n                title=\"Check in\" placement='bottom'>\n                <i class='mdi mdi-arrow-top-left-bold-outline topMenuIcons'></i>\n            </div>\n            <div v-show=\"!viewModel.studio.currentItem?.checkedOut\" @click=\"Checkout()\" data-toggle=\"tooltip\"\n                title=\"Check out\" placement='bottom'>\n                <i class='mdi mdi-arrow-bottom-right-bold-outline topMenuIcons'></i>\n            </div>\n            <div v-show=\"viewModel.studio.currentItem?.checkedOut\" @click=\"UndoCheckout()\" data-toggle=\"tooltip\"\n                title=\"Undo Check out\" placement='bottom'>\n                <i class='mdi mdi-arrow-bottom-left topMenuIcons'></i>\n            </div>\n            <div @click=\"Delete()\" data-toggle=\"tooltip\" title=\"Delete\" placement='bottom'>\n                <i style='color: red;' class='mdi mdi-delete-outline topMenuIcons'></i>\n            </div>\n            <div v-show=\"viewModel.studio.currentItem?.modelType=='qjson' && (viewModel.studio.currentItem.additionals as IQJsonAdditionals).qjsonType == 'qjson' && viewModel.studio.currentItem.usageType!='appSettings' \" @click=\"convertToCustomComp()\"\n                data-toggle=\"tooltip\"\n                title=\"Convert to Custom Component'\"\n                placement='bottom'>\n                <i style='color: #151233;' class='mdi mdi-swap-horizontal topMenuIcons'></i>\n            </div>\n            <!-- <div class=\"selectedVersionTextDiv\">\n                  <span v-show=\"viewModel.studio.currentItem?.version\" class=\"selectedVersionText\">Selected Version: {{viewModel.studio.currentItem?.version}}</span>\n            </div> -->\n            <template v-if=\"viewModel.studio.topMenu?.dynamicButtons\">\n                &nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;\n            </template>\n            <div v-for=\"item in viewModel.studio.topMenu?.dynamicButtons\" :key=\"item.ID\" @click=\"topMenuButton(item)\"\n                data-toggle=\"tooltip\" :title=\"item.tooltip\" placement='bottom'\n                class=\"topMenuTexts\"\n                :class=\"{ 'disabled-item': item.disabled, disabled: item.disabled }\">\n                {{item.name}}\n            </div>\n        </div>\n\n        <div v-show=\"!viewModel.studio.currentItem?.checkedOut\" class=\" viewModeWarning\" style='display: flex;'>\n\n\n\n            <i class='mdi mdi-alert-outline'\n                style=\"color:#FFA94A; font-size: 26px; margin-top: -6px; margin-left: 14px;\"></i>\n            <span class=\"viewModeTextFonts\" style=\"color:#616161; margin-left: 5px; margin-top: 4px;\">You are in <span\n                    style='font-weight:bold;'>Viewing</span> mode. Changes\n                will not be saved until </span>\n            <i style=\"font-size: 22px;\" class=\"mdi mdi-arrow-bottom-right-bold-outline warningCheckoutBtn\"\n                @click=\"Checkout()\" data-toggle=\"tooltip\" title=\"Check out\" placement='bottom'> </i>\n            <span\n                style='color:#616161;font-weight:bold; font-style:normal; margin-left: 5px; font-size: 14px; margin-top: 4px;'>Check\n                Out</span>\n\n        </div>\n\n    </div>\n</template>\n<style scoped>\n.disabled-item.disabled {\n    cursor: initial;\n    color: lightgrey;\n}\n\n.plateau-tooltip>.tooltip-inner {\n    background-color: red;\n}\n\n.topMenuDiv {\n    display: flex;\n    background-color: #F5F5F5;\n    height: 40px;\n    padding-top: 5px;\n}\n\n.topMenuIcons {\n    color: #151233;\n    font-size: 21px;\n    cursor: pointer;\n    padding: 6px;\n}\n.disabled-item.disabled.topMenuTexts {\n    pointer-events: none;\n}\n.topMenuTexts {\n    color: #151233;\n    /* font-size: 21px; */\n    cursor: pointer;\n    /* padding: 6px; */\n    margin:0px 5px 0px 5px;\n    user-select: none;\n}\n\n.viewModeWarning {\n    display: flex;\n    background-color: #FFFFFF;\n    height: 26px;\n    margin-top: 3px;\n    width: 520px;\n    border-radius: 4px;\n}\n\n.warningCheckoutBtn {\n    cursor: pointer;\n    color: #151233;\n    margin-top: -4px;\n}\n\n.viewModeTextFonts {\n    font-style: normal;\n    font-weight: 400;\n    font-size: 14px;\n    color: #616161;\n    text-align: left;\n    font-family: 'Roboto';\n    letter-spacing: 0px;\n    opacity: 1;\n}\n</style>"],"sourceRoot":""}]);
