___CSS_LOADER_EXPORT___.push([module.id, "\n.rootSection[data-v-1ae5374a] {\n    height: 100vh;\n    display: flex;\n    flex-flow: column;\n}\n.columnsSection[data-v-1ae5374a] {\n    display: flex;\n    flex: 1 1 auto;\n}\n.contentSection[data-v-1ae5374a] {\n    display: flex;\n    flex-direction: column;\n    height: 100%;\n    flex: 1;\n}\narticle[data-v-1ae5374a] {\n    height: 100%;\n    display: flex;\n    flex-direction: column;\n}\n.frameContainer[data-v-1ae5374a] {\n    flex: 1 1 0px;\n    width: 100%;\n}\niframe[data-v-1ae5374a] {\n    width: 100%;\n    height: 100%;\n    margin: 0px;\n    border: 0px;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/pages/studio.vue"],"names":[],"mappings":";AAiHA;IACI,aAAa;IACb,aAAa;IACb,iBAAiB;AACrB;AACA;IACI,aAAa;IACb,cAAc;AAClB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,YAAY;IACZ,OAAO;AACX;AAEA;IACI,YAAY;IACZ,aAAa;IACb,sBAAsB;AAC1B;AAEA;IACI,aAAa;IACb,WAAW;AACf;AAEA;IACI,WAAW;IACX,YAAY;IACZ,WAAW;IACX,WAAW;AACf","sourcesContent":["<script setup lang=\"ts\">\nimport { computed, defineAsyncComponent, onMounted, onUnmounted, Ref, ref, watch } from \"vue\";\nimport { container } from \"../../../domain/core/diContainer\";\nimport { IRouter } from \"../../../domain/presentation/IRouter\";\nimport { IViewModelManager } from \"../../../domain/viewModel/IViewModelManager\";\nimport { INavItem } from \"../../../domain/model/ui/INavItem\";\nimport { IViewModel } from \"../../../domain/viewModel/IViewModel\";\nimport { ILogger } from \"../../../domain/infrastructure/ILogger\";\nimport { IStudio } from \"../../../domain/useCase/IStudio\";\nimport { IUseCaseExecutor } from \"../../../domain/useCase/IUseCaseExecutor\";\n\nimport Navbar from \"../components/studio/navbar.vue\";\nimport TopMenu from \"../components/studio/topMenu.vue\";\nimport Header from \"../components/studio/header.vue\";\nimport { IApplication } from \"../../../domain/model/models\";\nimport { INotification } from \"../../../domain/presentation/INotification\";\n\nconst router = container.resolve<IRouter>(IRouter);\nconst studio = container.resolve<IStudio>(IStudio);\nconst notification = container.resolve<INotification>(INotification);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\n\nconst viewModelManager = container.resolve<IViewModelManager>(IViewModelManager);\nconst routerParams = router.getRouteParams<{ appID: string }>();\nconst logger = container.resolve<ILogger>(ILogger);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n\nconst frameContainerDiv = ref<HTMLDivElement>(null as any);\nconst navItems: Array<INavItem> = [{ name: \"files\", sidebarComponent: \"explorer.vue\" }];\n\nconst selectedApp = viewModelManager.findObject(routerParams.appID) as IApplication;\nconst prom = executor.execute(\n    async () => {\n        studio.nav_addNavItems(navItems);\n        await studio.openApp(routerParams.appID);\n    },\n    { loading: true }\n);\n\nconst beforeUnload = (event: any) => {\n    if (viewModel.studio.openItems && viewModel.studio.openItems.some((item) => item.checkedOut && item.modified?.model)) {\n        event.returnValue = \"\";\n    }\n};\n\nconst popState = async (event: any) => {\n    window.history.go(1);\n    if(document.getElementsByClassName(\"confirmation-dialog-container\").length != 0 || event.state?.replaced != false) return;\n    if (viewModel.studio.openItems && viewModel.studio.openItems.some((item) => item.checkedOut && item.modified?.model)) {\n        const result = await notification.showConfirm({\n            type: \"warning\",\n            title: `You are leaving the page.`,\n            text: \"Changes you made may not be saved.\",\n            showCloseIcon: true,\n            approveText: \"Confirm\",\n            cancelText: \"Cancel\",\n        });\n        if (result.result == \"cancel\") {\n            return;\n        }\n    }\n    window.history.go(-2);\n};\n\nonMounted(() => {\n    executor.execute(\n        async () => {\n            await prom;\n            studio.attach(frameContainerDiv.value);\n        },\n        { loading: false }\n    );\n    window.addEventListener(\"beforeunload\", beforeUnload);\n    window.history.pushState(null, \"\", window.location.href);\n    window.addEventListener(\"popstate\", popState);\n});\nonUnmounted(() => {\n    executor.execute(\n        async () => {\n            studio.nav_removeNavItems(navItems);\n            studio.detach();\n            viewModel.studio.appId = \"\";\n        },\n        { loading: false }\n    );\n    window.removeEventListener(\"beforeunload\", beforeUnload);\n    window.removeEventListener(\"popstate\", popState);\n});\n\nconst contentWidth = computed(() => {\n    const sidebar = viewModel.studio.ui?.sidebar;\n    if (!sidebar) {\n        return undefined;\n    }\n    return `calc(100% - ${sidebar.left + 40}px)`;\n});\n</script>\n\n<template>\n    <section class=\"rootSection\">\n        <Header :app=\"selectedApp\" />\n        <section class=\"columnsSection\">\n            <Navbar ref=\"navbar\"/>\n            <section class=\"contentSection\" :style=\"{ width: contentWidth }\">\n                <article>\n                    <TopMenu v-if=\"viewModel.studio.openItems?.length || 0\" />\n                    <div class=\"frameContainer\" ref=\"frameContainerDiv\"></div>\n                </article>\n            </section>\n        </section>\n    </section>\n</template>\n<style scoped>\n.rootSection {\n    height: 100vh;\n    display: flex;\n    flex-flow: column;\n}\n.columnsSection {\n    display: flex;\n    flex: 1 1 auto;\n}\n.contentSection {\n    display: flex;\n    flex-direction: column;\n    height: 100%;\n    flex: 1;\n}\n\narticle {\n    height: 100%;\n    display: flex;\n    flex-direction: column;\n}\n\n.frameContainer {\n    flex: 1 1 0px;\n    width: 100%;\n}\n\niframe {\n    width: 100%;\n    height: 100%;\n    margin: 0px;\n    border: 0px;\n}\n</style>\n"],"sourceRoot":""}]);
