: \"explorer.vue\", selected: true };\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst resizerDebounce = container.resolve<IDebounce>(IDebounce);\nconst navigationItems = viewModel!.studio!.ui?.navItems;\nnavigationItems?.forEach((item) => {\n  onNavItemSelect(item);\n});\ntype ITreeviewFilterData = Record<string, Array<IModel | IFolder>>;\nconst itemsCache = ref<Array<IModel | IModule | IFolder>>([]);\nlet cacheItems = true;\nconst treeviewFilterData = ref<ITreeviewFilterData>({});\nconst pagesFilterData = ref<ITreeviewFilterData>({});\nconst entitiesFilterData = ref<ITreeviewFilterData>({});\nconst processesFilterData = ref<ITreeviewFilterData>({});\nconst resetSideBar = ref(false);\nconst showPages = ref(true);\nconst showEntities = ref(true);\nconst showProcesses = ref(true);\nconst createPopupVisible = ref(false);\nconst filteredModels = ref<Array<ModelType>>([]);\nconst searchInput = ref(\"\");\nconst isClickedOut = ref(false);\nlet initialMenu = document.getElementsByClassName(\"vertical-nav-studio\")[0];\nlet expandedMenu = document.getElementsByClassName(\"vertical-nav-expanded\")[0];\nlet closeMenuIcon = document.getElementsByClassName(\"chevron-left\")[0];\nlet expandMenuIcon = document.getElementsByClassName(\"menu-icon\")[0];\nconst menuOpened = ref(false);\n\nwindow.addEventListener(\"click\", function (e) {\n  if (isClickedOut.value == true) {\n    createPopupVisible.value = false;\n  }\n});\nfunction onNavItemSelect(item: INavItem) {\n  if (item.selected && item.sidebarComponent) {\n    studio.nav_unselect();\n    sidebarManager.hide();\n    return;\n  }\n  if (item.sidebarComponent) {\n    sidebarManager.show(item.sidebarComponent);\n  } else {\n    sidebarManager.hide();\n  }\n  studio.nav_selectItem(item);\n}\nfunction openMenu() {\n  (document.getElementsByClassName(\"vertical-nav-studio\")[0] as any).style = \"display: none\";\n  (document.getElementsByClassName(\"vertical-nav-expanded\")[0] as any).style = \"display: block\";\n  menuOpened.value = true;\n}\nfunction closeMenu() {\n  (document.getElementsByClassName(\"vertical-nav-expanded\")[0] as any).style = \"display: none\";\n  (document.getElementsByClassName(\"vertical-nav-studio\")[0] as any).style = \"display: block\";\n  menuOpened.value = false;\n}\nconst debouncer = () => {\n  resizerDebounce.debounce(resizer, 400);\n};\nconst resizer = () => {\n  //a better css flex-box knowledge can completely remove this crazyness. sorry \n  const target = sidebarSectionElement.value!.firstElementChild?.nextElementSibling as HTMLElement | undefined;\n  if (!target) { setTimeout(() => resizer(), 1000); return; }\n  target.style.height = \"0px\";\n  setTimeout(() => {\n    const height = getComputedStyle(sidebarSectionElement.value!).height;\n    target.style.height = parseInt(height) - 12 + \"px\";\n  });\n};\nonMounted(() => {\n  (document.getElementsByClassName(\"vertical-nav-expanded\")[0] as any).style = \"display: none\";\n  document.getElementsByClassName(\"menu-icon\")[0].addEventListener(\"click\", openMenu);\n  document.getElementsByClassName(\"chevron-left\")[0].addEventListener(\"click\", closeMenu);\n  openMenu();\n  window.addEventListener(\"resize\", debouncer);\n  debouncer();\n});\nonUnmounted(() => {\n  window.removeEventListener(\"resize\", debouncer);\n})\n\nfunction moveHandler(event: MouseEvent) {\n  if (!ui) {\n    return;\n  }\n  ui.sidebar.left = event.x - sidebarSectionElement.value!.offsetLeft - 2;\n}\nfunction mousedown(event: MouseEvent) {\n  window.addEventListener(\"mousemove\", moveHandler);\n  event.preventDefault();\n}\nfunction mouseup() {\n  window.removeEventListener(\"mousemove\", moveHandler);\n}\nasync function collapse() {\n  const treeview = await executor.resolveUseCase<ITreeView>(ITreeViewInstances.FileExplorer);\n  treeview.collapseAll();\n}\nasync function addModule() {\n  createPopupVisible.value = false;\n  const fileExplorer = await executor.resolveUseCase<IFileExplorer>(IFileExplorer);\n  fileExplorer.onNewItemSelect(viewModel.studio.appId, viewModel.studio.appName, \"module\");\n}\nasync function addScreen() {\n  createPopupVisible.value = false;\n  const fileExplorer = await executor.resolveUseCase<IFileExplorer>(IFileExplorer);\n  fileExplorer.onNewItemSelect(viewModel.studio.appId, viewModel.studio.appName, \"qjson\");\n}\n\nasync function attachModuleToApp() {\n  createPopupVisible.value = false;\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../dialogs/attachModuleToApp.vue`)),\n    {\n      closable: true,\n      title: \"Import Existing Module\",\n      width: \"1000px\",\n      closeOnOutClick: true,\n    }\n  );\n}\n\nconst leftpx = computed(() => {\n  if (!ui) {\n    return undefined;\n  }\n  return ui.sidebar.left + \"px}\";\n});\n\nfunction openSettings() {\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../dialogs/deploySettings.vue`)),\n    { closable: true, title: \"Deploy Settings\", width: \"1100px\", height: \"497px\" }\n  );\n}\n\nfunction filterTreeview() {\n  if (itemsCache.value.length < viewModel.studio.items.length) {\n    itemsCache.value = itemsCache.value.concat(viewModel.studio.items.filter((item) => itemsCache.value.findIndex((i) => i.ID == item.ID) == -1));\n  } else if (itemsCache.value.filter.length > viewModel.studio.items.length) {\n    itemsCache.value = itemsCache.value.filter(\n      (item) => item.objectType != \"module\" || viewModel.studio.items.findIndex((i) => i.ID == item.ID) != -1\n    );\n  }\n  treeviewFilterData.value = {};\n  pagesFilterData.value = {};\n  entitiesFilterData.value = {};\n  processesFilterData.value = {};\n\n  itemsCache.value.forEach((item) => {\n    switch (item.objectType) {\n      case \"module\":\n        pagesFilterData.value[item.ID] = [];\n        entitiesFilterData.value[item.ID] = [];\n        processesFilterData.value[item.ID] = [];\n        (item as IModule).children.forEach((children)